@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Avenir";
  font-display: swap;
  src: url("../fonts/AvenirNextCyr-Medium.woff") format("woff2"), url("../fonts/AvenirNextCyr-Medium.woff2") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Avenir", "Arial", sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #000000;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 7px 6px;
}
@media (max-width: 1439px) {
  .container {
    padding: 7px 6px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 7px 8px;
  }
}

.preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #000000;
  z-index: 100000;
}

.preloader__container {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.preloader__item {
  position: absolute;
  display: block;
  animation: preloader-rotate 3s infinite linear;
}
@media (max-width: 1279px) {
  .preloader__item {
    width: 200px;
  }
  .preloader__item img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
.preloader.hide-preloader {
  animation: hide-preloader 1s;
}

.preloader.preloader-hidden {
  display: none;
}

@keyframes hide-preloader {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.btn {
  display: inline-flex;
  padding: 6px 7px;
  color: #ffffff;
  background-color: #EC6608;
  border: none;
  border-radius: 73px;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.intro .container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 6px;
}
@media (max-width: 1023px) {
  .intro .container {
    display: flex;
    flex-direction: column;
  }
}

.intro__video-block {
  grid-column: 1/3;
}

.intro__video-wrapper {
  background: url("../img/video-cover.png") center no-repeat;
  background-size: cover;
  height: 559px;
  max-height: 559px;
  overflow: hidden;
  border-radius: 20px;
}
@media (max-width: 767px) {
  .intro__video-wrapper {
    height: fit-content;
    max-height: fit-content;
  }
}
.intro__video-wrapper video {
  object-fit: cover;
  height: 559px;
}
@media (max-width: 767px) {
  .intro__video-wrapper video {
    height: 236px;
  }
}

.intro__block {
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 54px;
  min-height: 445px;
}
@media (max-width: 1023px) {
  .intro__block {
    padding: 20px 7px;
    min-height: 222px;
  }
}

.intro__primary-block {
  grid-column: 1/2;
  background: url("../img/primary-cover.png") center no-repeat;
  background-size: cover;
}
@media (max-width: 1023px) {
  .intro__primary-block .intro__logo {
    width: 141px;
    height: 28px;
  }
}

.intro__secondary-block {
  grid-column: 2/3;
  background: url("../img/secondary-cover.png") center no-repeat;
  background-size: cover;
}
@media (max-width: 1023px) {
  .intro__secondary-block .intro__logo {
    width: 212px;
    height: 28px;
  }
}

.intro__list {
  text-align: center;
}

.intro__item {
  font-family: "Avenir", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 1.39;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .intro__item {
    font-size: 0.75rem;
    line-height: 1.42;
  }
}
.intro__item b {
  color: #EC6608;
  font-weight: 500;
}

.intro__btn {
  display: flex;
  align-items: center;
}
.intro__btn span {
  font-family: "Avenir", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.154rem;
  line-height: 1.35;
  color: #ffffff;
  padding: 0 10px;
}
@media (max-width: 1023px) {
  .intro__btn span {
    font-size: 0.75rem;
    line-height: 1.33;
  }
}

.intro__btn-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  background-color: #ffffff;
}
@media (max-width: 1023px) {
  .intro__btn-icon {
    width: 25px;
    height: 25px;
  }
}
@media (max-width: 1023px) {
  .intro__btn-icon svg {
    width: 15px;
    height: 9px;
  }
}